textarea.md-input {
  min-height: 80px;
  resize: none;
  overflow: hidden;
  -webkit-transition: height 200ms ease-out;
  -o-transition: height 200ms ease-out;
  transition: height 200ms ease-out;
  line-height: 24px;
}

textarea.no_autosize {
  min-height: inherit;
  overflow: auto;
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
  resize: both;
}

.md-input-wrapper {
  position: relative;
  padding-top: 4px;
  width: 100%;
  display: block;
}

.md-input-wrapper .md-input-bar {
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}

.md-input-wrapper>label:before,
.md-input-wrapper>label:after {
  background: #c2185b;
}

.md-input-wrapper .md-input-bar:before,
.md-input-wrapper .md-input-bar:after {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  width: 0;
  height: 2px;
  background: #c2185b;
  -webkit-transition: width 300ms cubic-bezier(0.4, 0, 0.2, 1);
  -o-transition: width 300ms cubic-bezier(0.4, 0, 0.2, 1);
  transition: width 300ms cubic-bezier(0.4, 0, 0.2, 1);
}

.md-input-wrapper .md-input-bar:before {
  left: 50%;
}

.md-input-wrapper .md-input-bar:after {
  right: 50%;
}

.md-input-wrapper .md-input-bar.uk-form-width-mini {
  width: 40px;
}

.md-input-wrapper .md-input-bar.uk-form-width-small {
  width: 130px;
}

.md-input-wrapper .md-input-bar.uk-form-width-medium {
  width: 200px;
}

.md-input-wrapper .md-input-bar.uk-form-width-large {
  width: 500px;
}

.md-input-wrapper>label {
  color: #727272;
  position: absolute;
  top: 16px;
  left: 14px;
  right: 0;
  pointer-events: none;
  -webkit-transition: all 150ms ease-out;
  -o-transition: all 150ms ease-out;
  transition: all 150ms ease-out;
}

.md-input-wrapper+* {
  margin-top: 10px;
}

.md-input-wrapper.md-input-wrapper-disabled>label {
  color: rgba(0, 0, 0, 0.26);
}

.md-input-wrapper-count {
  padding-bottom: 24px;
}

.md-input-wrapper-count .md-input-bar {
  bottom: 24px;
}

.md-input-wrapper-count .text-count-wrapper {
  font-size: 12px;
  position: absolute;
  right: 0;
  bottom: 0;
  opacity: 0;
  -webkit-transition: opacity 200ms ease-in;
  -o-transition: opacity 200ms ease-in;
  transition: opacity 200ms ease-in;
}

.md-input-wrapper-count>.md-input-wrapper-count .text-count-wrapper {
  position: absolute;
  bottom: 0;
  right: 0;
}

.md-input-filled>label,
.md-input-focus>label {
  top: 8px;
  /* top: -6px; */
  font-size: 12px;
}

.md-input-filled.md-input-wrapper-count .text-count-wrapper,
.md-input-focus.md-input-wrapper-count .text-count-wrapper {
  opacity: 1;
}

.md-input-focus .md-input-bar:before,
.md-input-focus .md-input-bar:after {
  width: 50%;
}

.md-input-wrapper-danger .md-input-bar:before,
.md-input-wrapper-danger .md-input-bar:after {
  background: #e53935;
}

.md-input-wrapper-danger.md-input-wrapper-count .text-count-wrapper {
  color: #e53935;
}

.md-input-wrapper-success .md-input-bar:before,
.md-input-wrapper-success .md-input-bar:after {
  background: #7cb342;
}

.md-input-wrapper-success.md-input-wrapper-count .text-count-wrapper {
  color: #7cb342;
}

/* Custom Jean */

.md-input-icon {
  z-index: 20;
  position: absolute;
  top: 16px;
  left: 15px;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  color: rgba(0, 0, 0, .54);
}

.md-input-wrapper {
  border-radius: 4px 4px 0 0;
  background-color: #f5f5f5;
  display: -ms-inline-flexbox;
  display: inline-flex;
  position: relative;
  height: 56px;
  /* margin-top: 16px; */
  overflow: hidden;
}

.md-input-wrapper-outlined {
  border-style: solid;
  border-radius: 4px 4px;
  border-width: 1px;
  overflow: inherit;
  background-color: rgba(0, 0, 0, 0);
}

.md-input-wrapper-no-bg {
  background-color: rgba(0, 0, 0, 0.2);
}

.md-input-wrapper-with-icon>label {
  left: 48px;
  right: auto;
}
